import React, { memo } from "react";
import { Fonts, styled, useNavigationParams } from "@true/core";
import { Icons } from "@true/asset";
import { useBottomSheetNavigator } from "@true/ui";
import { ImageSourcePropType, View } from "react-native";
import {BottomSheetView} from '@true/ui';

const ItemAction = memo(function ItemAction(props: {
  icon: ImageSourcePropType;
  text: string;
}) {
  const { icon, text } = props;
  return (
    <SItemContainer>
      <View style={{ alignItems: "flex-start", flexDirection: "row" }}>
        <SIcon>
          <SImage source={icon} />
        </SIcon>
        <STextView>
          <SText>{text}</SText>
        </STextView>
      </View>
    </SItemContainer>
  );
});

export interface ChannelActionScreenParams {
  isCompany: boolean;
}

export const ChannelActionScreen = memo(function ChannelActionScreen() {
  const { isCompany } = useNavigationParams<ChannelActionScreenParams>();
  const { onViewLayout } = useBottomSheetNavigator();

  return (
    <BottomSheetView onLayout={onViewLayout("ChannelAction")}>
      <ItemAction icon={Icons.Filled.USER_PLUS} text={"Invite member"} />
      {isCompany ? null : <ItemAction icon={Icons.Outlined.ARROW_RIGHT_ON_RECTANGLE} text={"Leave channel"} />}
    </BottomSheetView>
  );
});
const SItemContainer = styled.TouchableOpacity`
  padding-left: 16px;
  margin-top: 16px;
`;
const SIcon = styled.View`
  width: 24px;
  height: 24px;
  align-items: center;
  justify-content: center;
`;
const SImage = styled.Image`
  width: 24px;
  height: 24px;
`;
const STextView = styled.View`
  padding-bottom: 16px;
  border-bottom-width: 1px;
  border-bottom-color: #ebebeb;
  flex: 1;
  margin-right: 16px;
  margin-left: 8px;
`;
const SText = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242424;
`;
